<template>
  <button v-style="styles.button"
          @focus="handleFocus"
          @blur="handleBlur"
          @click="handleClick($event)"
          @mouseenter="handleMouseEnter"
          @mouseleave="handleMouseLeave"
          tabindex="0"
          :disabled="disabled">
    <slot name="before"></slot>
    <label v-style="styles.label" v-if="label">
      {{label}}
    </label>
    <slot name="after"></slot>
    <slot></slot>
    <m-ripple
      :from-center="fromCenter"
      :ripple-color="rippleColor"
      :show-focus-ripple="showFocusRipple">
    </m-ripple>
  </button>
</template>

<script type="text/babel">
  import jss from '../../util/jss'
  import color from 'color'
  import '../../directives'
  import mRipple from '../ripple/Ripple.vue'
  import themeManager from '../../styles/theme-manager'
  import styleUtil from '../../styles/util'
  import { defaultProps, any, oneOf, oneOfType } from '../../util/prop'

  let muiTheme = themeManager.getTheme()
  let {buttonFilterColor} = muiTheme
  let tabPressed = false
  window.addEventListener('keydown', () => {
    tabPressed = true
  })
  export default {
    /**
     * slot,[before after],after
     * @example
     * <m-flat-button label="Default"><img /></m-flat-button>
     * 
     * <m-flat-button label="Default"><img slot="after" /></m-flat-button>
     *
     * <m-flat-button label="Default"><img slot="before" /></m-flat-button>
     */
    props: defaultProps({
      /**
       * true
       */
      disabled: false,
      /**
       * 
       */
      label: String,
      /**
       * Buttonlabel
       *
       */
      labelStyle: Object,
      /**
       * url
       */
      href: String,

      /**
       * 
       */
      fromCenter: false,

      onClick: () => {},

      onBlur: () => {},
      /**
       * ,
       */
      onKeyboardFocus: () => {},
      /**
       * 
       */
      onMouseEnter: () => {},
      /**
       * 
       */
      onMouseLeave: () => {},
      /**
       * true,primaryTextColor
       */
      primary: false,
      /**
       * true,secondaryTextColor primarytruesecondary
       */
      secondary: false,
      /**
       * 
       */
      rippleColor: buttonFilterColor,
      /**
       * 
       */
      style: oneOfType([Object, Array])
    }),
    data () {
      return {
        showFocusRipple: false
      }
    },
    computed: {
      styles () {
        const {disabledColor, primaryColor, secondaryColor} = muiTheme
        let color = ''
        if (this.disabled) {
          color = disabledColor
        } else if (this.primary) {
          color = primaryColor
        } else if (this.secondary) {
          color = secondaryColor
        }
        return {
          button: styleUtil.mergeStyle({
            border: 'none',
            outline: 'none',
            position: 'relative',
            userSelect: 'none',
            backgroundColor: 'initial',
            color,
            pointerEvents: this.disabled ? 'none' : '',
            borderRadius: 2,
            padding: 0,
            lineHeight: '36px'
          }, this.style),
          label: styleUtil.mergeStyle({
            paddingLeft: 16,
            paddingRight: 16,
            fontSize: 14
          }, this.labelStyle)
        }
      }
    },
    watch: {},
    methods: {
      handleBlur () {
        clearTimeout(this.focusTimeout)
        this.showFocusRipple = false
        this.onBlur()
      },
      handleFocus () {
        //focus, focus, click,,focus
        //Tab,Button100ms,
        this.focusTimeout = setTimeout(() => {
          if (tabPressed) {
            this.showFocusRipple = true
            this.onKeyboardFocus()
          }
        }, 100)
      },
      handleClick ($event) {
        if (this.href) {
          window.location = this.href
        }
        tabPressed = false
        clearTimeout(this.focusTimeout)
        this.showFocusRipple = false
        this.onClick($event)
      },
      handleMouseEnter (event) {
        this.onMouseEnter && this.onMouseEnter(event)
      },
      handleMouseLeave (event) {
        this.onMouseLeave && this.onMouseLeave(event)
      }
    },
    components: {
      mRipple
    },
    ready () {
    }
  }
</script>



