import fn from './fn'
import { typeOf, isObject, isFunction, isArray } from './type'

let {map} = fn
/**
 * prop
 * @param {Object} propObj
 * @param {String} propObjkey 
 * @param {Any} proObjvalue ,
 * @example
 *  defaultProps({
 *    name:String,
 *    color:'red'
 *  })
 *  => {
 *    name:{
 *      type:String
 *    },
 *    color:{
 *      default:'red',
 *      type:String
 *    }
 *  }
 *
 */
export function defaultProps (propObj) {
  return map(propObj, (key, value) => {
    let result
    if (isObject(value)) {
      //vueprop
      if (value.type || value.default || value.validator || value.twoWay || value.required || value.coerce) {
        result = value
      } else {
        result = {
          type: Object,
          default: () => value
        }
      }
    } else {
      let type
      //value[String,Number...]
      if (value
        && isFunction(value)
        && window[value.name]
        && /native code/.test(value.toString())) {
        type = value
      } else {
        type = typeOf(value)
      }
      result = {
        type,
        default: value
      }
    }
    return result
  })
}
/**
 *
 * @param {Array} valueList
 * @param {Any} defaultValuevalueList
 * @returns {Object}
 */
export function oneOf (valueList, defaultValue) {
  return {
    default: defaultValue,
    validator (value) {
      return ~valueList.indexOf(value)
    }
  }
}
export function oneOfType (typeList, defaultValue) {
  return {
    type: typeList,
    default: defaultValue
  }
}
export const any = {
  validator: function () {
    return true
  }
}



